	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Java is no longer alone on the JVM. It now has to share space with its younger siblings JRuby, Jython, Groovy, Clojure, Scala and many others. Each new language has its own set of advantages and disadvantages compared to Java, and its own niche in which it performs best, but Scala in particular is gaining momentum.\n\nIn this presentation to San Francisco Java User Group on January 18, 2012, Marakana's own Dan Rosen introduces Scala to Java developers and shows off some of its expressive power -- higher-order functions, type inference, and pattern matching -- by building a simple web service. You'll never look at Java the same way.\n\nSlides from this presentation available here http://bit.ly/ztgVxa","meta":{"duration":"PT1H32M35S"},"presenter":{"name":null},"source":{"id":"PKc5IwHG68k","provider":"youtube"},"slug":"scala-versus-java","tag":["scala","java","marakana","techtv","typesafe","dan rosen","sbt","jdk"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/PKc5IwHG68k/default.jpg","height":90,"width":120}},"title":"Scala Versus Java"}},{"node":{"description":"Paul Phillips is a co-founder of Typesafe and the most prolific committer to Scala. He's spent the last 5 years developing the language, writing a lot of code and drawing a lot of conclusions, but has decided to walk away. In this talk, he explains why.\n\nHe outlines what he believes to be certain shortcomings of the Scala collections library. In Paul's words, \"Based on my extensive experience with Scala collections, I'm writing my own. The focus is much tighter: immutable, performant, predictable, correct. The talk will alternate between why the Scala collections manage none of those things, and how I hope to do better.\"\n\nA day after the SF Scala event, Paul left this comment on their meetup page, which seems relevant, \"[...] I already discovered that if I say nothing about it then people will draw very incorrect conclusions about what the major issues are and why I've moved on. It's not sad, it's not happy, it's only a question of whether you want the real picture or some distant reflection of it.\"\n\nFollow along with Paul's slides: http://crcl.to/zdybs\n\nFree Scala resources: http://crcl.to/zhwdp\nProfessional Scala training: http://crcl.to/w6kf7\n\nInformation on SF Scala: http://sfscala.org","meta":{"duration":"PT45M42S"},"presenter":{"name":null},"source":{"id":"uiJycy6dFSQ","provider":"youtube"},"slug":"scala-collections-why-not","tag":["paul phillips","typesafe","scala","collections","sf scala","newcircle"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/uiJycy6dFSQ/default.jpg","height":90,"width":120}},"title":"Scala Collections: Why Not?"}}]}},"pathContext":{"slug":"typesafe","tag":"typesafe"}}

